<template>
	<div style="width: 100%;padding-top: 40px;">
		<div style="width: 1280px; margin: 0 auto;">
			<div style="overflow: hidden; width: 100%;height: auto;margin-bottom: 100px;">
				<div style="background-color: #ededed;font-size: 13px;" class="sysysp">
					首页 / 所有商品
				</div>
				<div v-for="i in pzarr" class="sp">
					<span @click="">{{i.title}}</span>
					<span>{{i.p1}}</span>
					<span>{{i.p2}}</span>
					<span>{{i.p3}}</span>
					<span>{{i.p4}}</span>
				</div>
				<div v-for="i in ylarr" class="sp">
					<span>{{i.title}}</span>
					<span>{{i.p1}}</span>
					<span>{{i.p2}}</span>
					<span>{{i.p3}}</span>
					<span>{{i.p4}}</span>
				</div>
				<div v-for="i in cdarr" class="sp">
					<span>{{i.title}}</span>
					<span>{{i.p1}}</span>
					<span>{{i.p2}}</span>
					<span>{{i.p3}}</span>
					<span>{{i.p4}}</span>
				</div>
				<div v-for="i in kwarr" class="sp">
					<span>{{i.title}}</span>
					<span>{{i.p1}}</span>
					<span>{{i.p2}}</span>
					<span>{{i.p3}}</span>
					<span>{{i.p4}}</span>
				</div>
				<div v-for="(i,$index) in g_arr" style="margin-top: 29px;">
				<div class="hj">
					<a href="#/gwc"><img :src="i.img" @click="a_arr(g_arr[$index])" style="width: 214px;height: 214px;"></a>
					<p>{{i.title}}</p>
					<span>{{i.text}}</span>
				</div>
			</div>
			</div>
			
		</div>
	</div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
	export default{
		data(){
			return{
				pzarr:[{title:"品种",p1:"红葡萄酒",p2:"白葡萄酒",p3:"起泡酒/香槟",p4:"桃红葡萄酒"}],
				ylarr:[{title:'原料成分',p1:"马尔贝克",p2:"辛索",p3:"琼瑶浆",p4:"白苏维翁"}],
				cdarr:[{title:'产地',p1:"匈牙利",p2:"西班牙",p3:"法国",p4:"中国",p4:"澳大利亚"}],
				kwarr:[{title:'口味',p1:"半干葡萄酒",p2:"半甜葡萄酒",p3:"干葡萄酒",p4:"甜葡萄酒"}]
			}
		},
		computed:{
			...mapGetters(['g_arr'])
		},
		methods:{
			...mapActions(['a_arr'])

		}
	};
</script>
<style>
	.sp span:nth-child(1){
		color: #919191;
	    font-family: 宋体;
	    font-size: 12px;
	    background-color: #ededed;
	    border-top: 0px solid #dddddd;
	    border-right: 0px solid #dddddd;
	    border-bottom: 1px solid #dddddd;
	    border-left: 0px solid #dddddd;
	    min-width: 80px;
	    padding:10px 10px;
	    display: inline-block;
	    margin:0;
	}
	.sp span{
		color:#666666;
		margin: 5px;
		padding: 2px 25px;
		font-size: 12px;
	}
	.sp{
		border-top: 0px solid #e2e2e2;
	    border-right: 0px solid #e2e2e2;
	    border-bottom: 1px solid #e2e2e2;
	    border-left: 0px solid #e2e2e2;
	}
	.sysysp{
		margin-top: 0;
	    margin-bottom: 0;
	    padding-top: 10px;
	    padding-bottom: 10px;
	    padding-right: 0;
	    padding-left: 9px;
	    color:#666666;
	}
	.hj{
		border: 1px solid #ededed;
    	margin: 0 10px 15px 0;
    	float:left;
    	width:19%;
	}
	.hj p{
		font-size: 14px;
	    font-weight: bold;
	    color: #dd3333;
	    font-family: arial;
	    margin-bottom: 6px;
	    padding-left:6px;
	}
	.hj span{
		font-size: 13px;
	    font-weight: normal;
	    color: #494949;
	    font-family: 宋体;
	    margin-bottom: 6px;
	    display:inline-block;
	    padding-left:6px;
	}
</style>


