						<p>{{i.content1}}</p>
						<p>{{i.content1_1}}</p>
						<p style="margin-top: 25px;">{{i.content2}}</p>
						<p>{{i.content2_2}}</p>
						<p style="margin-top: 25px;">{{i.content3}}</p>
						<p style="margin-bottom: 60px;">{{i.content3_3}}</p>
					</div>
				</div>


				<div v-for='i in zfarr2' class="zf1">
					<h3 style="font-size: 24px;margin-top: 60px;">{{i.zf}}</h3>
					<p style="margin-bottom: 52px;color:#A9A9A9;">{{i.zffs}}</p>
					<div class="zftext">
						<p style="margin-bottom: 25px;">{{i.title}}</p>
						<p>{{i.content1}}</p>
						<p>{{i.content1_1}}</p>
						<p style="margin-top: 25px;">{{i.content2}}</p>
						<p>{{i.content2_2}}</p>
						<p style="margin-top: 25px;margin-bottom: 60px;">{{i.content3}}</p>
					</div>
				</div>

