var getters = {
	g_arr(state){
		return state.sysparr
	},
	g_arr2(state){
		return state.arrss
	},
	g_arr3(state){
		return state.gwcarr
	}
}
var mutations = {
	m_arr(state,a){
		state.arrss=[]
		state.arrss.push(a)
	},
	m_arr2(state,a){
		// console.log(state.arrss);
		var fas=true
		for (var i=0;i<state.gwcarr.length;i++) {
			if(state.gwcarr[i].title==a.title){
				state.gwcarr[i].uid++
				fas=false
			}
		}
			if(fas){
				a.uid=1
			state.gwcarr.push(a)
			}
	},
	m_arr3(state,a){
		state.gwcarr=[]
	},
	delete(state,{title}){
		state.gwcarr.forEach((n,i)=>{
			if (n.title==title) {
				state.gwcarr.splice(i,1)
			}
		})
	}

}
var actions = {
	a_arr({commit},a){
		commit('m_arr',a)
	},
	a_arr2({commit},a){
		commit('m_arr2',a)
	},
	a_arr3({commit},a){
		commit('m_arr3',a)
		console.log(a);
	},
	delproduct({commit},a){
		commit('delete',{
			title:a.title
		})
	}
}
export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})


