<template>
	<div id="nav" style="box-sizing: border-box">
		<div v-for="(i,$index) in arr"  @click='btn($index)'>
			<a :href="'#'+i.path" :class="{active:index==$index}">
				{{i.title}}
			</a>
		</div>
	</div>
</template>

<script>
	export default{
		props:['appnav'],
		data(){
			return{
				index:0,
				arr:this.appnav
			}
		},
		methods:{
			btn(a){
				this.index=a
			}
		}
	}
</script>

<style>
	*{
		margin: 0;
		padding: 0;
		text-decoration: none;
	}
	#nav div{
		float:left;
		margin: 5px 5px 0 0;
		line-height: 46px;
		text-align: center;
	    box-sizing: border-box;
	    font-size:14px;
	    width: 100px;
	}
	#nav div a{
		color:#fff;
	    min-width: 100px;
	    padding: 15px 15px;
	}
	.active{
		background-color:#5e0000;
	}
</style>


