import Vue from 'vue'
import Router from 'vue-router'
import SY from './SY.vue'
import DT from './DT.vue'
import SYSP from './SYSP.vue'
import ZF from './ZF.vue'
import GY from './GY.vue'
import XX from './XX.vue'
import gwc from './gwc.vue'
Vue.use(Router)
var router = new Router({
	routes:[
		{path:'/',name:'SY',component:SY},
		{path:'/DT',name:'DT',component:DT},
		{path:'/SYSP',name:'SYSP',component:SYSP},
		{path:'/ZF',name:'ZF',component:ZF},
		{path:'/GY',name:'GY',component:GY},
		{path:'/XX',name:'XX',component:XX},
		{path:'/gwc',name:'gwc',component:gwc}
	]
})
export default router


